  span {
    font-weight: 500;
  }
  .productions__right-acc__btn {
    width: 38px;
    height: 38px;
    border: 1px solid #000;
    box-sizing: border-box;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    cursor: pointer;

    &::before,
    &::after {
      content: '';
      width: 17px;
      height: 2px;
      background: #000;

      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &::after {
      transform: translate(-50%, -30%) rotate(90deg);
    }
  }
}

