.page-gallery {
  .page-h2 {
    margin: 0;
    padding: 40px 0;

    @media screen and (max-width: 992px) {
      padding: 20px 0;
    }
  }

  &__wrapper {
    border-top: 2px solid $color-blue-charcoal;
    padding-top: 54px;
    padding-bottom: 10%;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    // grid-template-rows: repeat(12, 1fr);
    // grid-column-gap: 0px;
    // grid-row-gap: 0px;
    gap: 40px;

    @media screen and (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    display: block !important;
    position: relative;

    div {
      display: none;
    }

    &-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &-span {
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      color: white;
      padding: 16px 20px;
      background-color: $color-blue-charcoal;
      background: rgba($color-blue-charcoal, 0.69);
    }

    &--1 {
      @media screen and (min-width: 992px) {
        grid-area: 1 / 1 / 4 / 2;
      }
    }
    &--2 {
      @media screen and (min-width: 992px) {
        grid-area: 1 / 2 / 6 / 3;
      }
    }
    &--3 {
      @media screen and (min-width: 992px) {
        grid-area: 1 / 3 / 5 / 4;
      }
    }
    &--4 {
      @media screen and (min-width: 992px) {
        grid-area: 4 / 1 / 9 / 2;
      }
    }
    &--5 {
      @media screen and (min-width: 992px) {
        grid-area: 6 / 2 / 10 / 3;
      }
    }
    &--6 {
      @media screen and (min-width: 992px) {
        grid-area: 5 / 3 / 8 / 4;
      }
    }
    &--7 {
      @media screen and (min-width: 992px) {
        grid-area: 9 / 1 / 13 / 2;
      }
    }
    &--8 {
      @media screen and (min-width: 992px) {
        grid-area: 10 / 2 / 13 / 3;
      }
    }
    &--9 {
      @media screen and (min-width: 992px) {
        grid-area: 8 / 3 / 13 / 4;
      }
    }
  }

  &__root {
    .fancybox-container {
      .page-gallery__item-block {
        position: relative;
        padding: 34px 0 0 0;
      }
    }

    .fancybox-content {
      background-color: transparent;
      // width: 50%;
      // max-height: 400px;
      width: 700px;
      height: 500px;

      .page-gallery__item-span {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        color: white;
        padding: 16px 20px;
        background-color: $color-blue-charcoal;
        background: rgba($color-blue-charcoal, 0.69);
      }

      // .page-gallery__item-img {
      //   width: 100%;
      //   height: 100%;
      //   object-fit: cover;
      // }
    }

    .fancybox-bg {
      background-color: rgba(#ffffff, 0.8);
    }

    .fancybox-navigation {
      .fancybox-button {
        // background-clip: content-box;
        // height: 100px;
        // opacity: 0;
        // position: absolute;
        top: 78%;
        color: $color-blue-charcoal;

        &:disabled {
          opacity: .4;
        }

        // &:hover {
        //   color: $color-blue-charcoal;
        // }
        
        &--arrow_left,
        &--arrow_right {
          background-color: transparent;
          width: 40px;
          height: 40px;
          padding: 0;
        }

        &--arrow_left {
          left: 32%;
          // padding: 31px 26px 31px 6px;
          // padding: 20px;
        }

        &--arrow_right {
          right: 32%;
          // padding: 31px 26px 31px 6px;
          // padding: 20px;
        }

        // &.fancybox-close-small {
        //   border-radius: 50%;
        //   border: 1px solid black;
        // }
      }
    }

    .fancybox-button.fancybox-close-small {
      border-radius: 50%;
      border: 1px solid black;
      padding: 0;
      width: 24px;
      height: 24px;
    }
  }
