.breadcrumbs {
  color: $color-blue-charcoal;
  padding-top: 40px;
  font-size: 18px;

  @media screen and (max-width: 1200px) {
    font-size: 16px;
  }

  @media screen and (max-width: 992px) {
    font-size: 14px;
  }
  
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }

  &__wrapper {
  }

  &__link {
    // &-current {
    //   opacity: .5;
    // }

    &:last-child {
      opacity: 0.5;
    }

    & + .breadcrumbs__link {
      &::before {
        content: "/";
        display: inline-block;
        padding-right: 5px;
        margin-left: 5px;
      }
    }
  }
}
