@mixin page-h2 {
  color: #001017;
  /* 56 Med */
  font-size: 56px;
  font-family: 'Muller';
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 2.8px;
  text-transform: uppercase;

  @media screen and (max-width: 1400px) {
    font-size: 45px;
  }

  @media screen and (max-width: 992px) {
    font-size: 30px;
  }
  
  @media screen and (max-width: 567px) {
    font-size: 20px;
  }
}

@mixin page-text-24 {
  color: #001017;
  /* 24 Reg */
  font-size: 24px;
  font-weight: 400;
  line-height: 150%;

  @media screen and (max-width: 1400px) {
    font-size: 20px;
  }
  
  @media screen and (max-width: 576px) {
    font-size: 14px;
  }
}

@mixin page-text-18 {
  color: #001017;
  font-size: 18px;
  font-weight: 500;
  line-height: 150%;

  @media screen and (max-width: 1400px) {
    font-size: 14px;
  }
  
  // @media screen and (max-width: 768px) {
  //   font-size: 10px;
  // }
  
  @media screen and (max-width: 576px) {
    font-size: 10px;
  }
}

@mixin page-btn {
  border-radius: 4px;
  border: 2px solid #001017;
  padding: 20px 40px;
  box-sizing: border-box;
  outline: none;

  color: #001017;
  font-size: 24px;
  font-weight: 500;
  line-height: normal;  
  
  text-decoration: none;
  display: inline-block;

  @media screen and (max-width: 1400px) {
    padding: 15px 30px;
    font-size: 20px;
  }
}

@mixin page-btn-black {
  border-radius: 4px;
  border: 2px solid #001017;
  background: #001017;
  padding: 20px 40px;
  box-sizing: border-box;
  outline: none;

  color: #fff;
  font-size: 24px;
  font-weight: 500;
  line-height: normal;  
  
  text-decoration: none;
  display: inline-block;

  @media screen and (max-width: 1400px) {
    padding: 15px 30px;
    font-size: 20px;
  }
}