.questions {
  background: #fff;
  padding-top: 200px;
  .container {

  }
  .questions__wrapper {

  }
  .questions__header {
    padding-bottom: 40px;
    margin-bottom: 41px;
    margin-top: 0;
    border-bottom: 2px solid #001017;
  }
  .questions__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 100px;
  }
  .questions__left {

  }
  .questions__text {
    opacity: 0.699999988079071; 
  }
  .questions__button {
    padding: 15px 30px;
    display: inline-flex;
    justify-self: start;
    align-items: center;

    font-family: Muller;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: normal; 
    
    img {
      margin-right: 10px;
    }
  }

  .questions__right {

  }
  .questions__form {

  }
  .questions__fields {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 31px 40px;
  }
  .questions__label {

  }
  .questions__field-text {
    display: block;
    opacity: 0.699999988079071;
    margin-bottom: 10px;     
  }
  .questions__field {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 4px;
    border: 2px solid #001017;
    background: #FFF;    
  }

  .questions__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
  }
  .questions__terms {
    opacity: 0.699999988079071;

    a {
      text-decoration: underline;
      opacity: 1;
    }
  }
}

.footer {
  background: #fff;
  padding: 200px 0 50px;

  .container {

  }
  .footer__wrapper {
    display: grid;
    grid-template-columns: 2fr 8fr 2.2fr 2.2fr;
    gap: 40px 90px;
    align-items: start;
    border-radius: 4px;
    background: #001017;
    backdrop-filter: blur(5px);
    padding: 40px;
    box-sizing: border-box;
  }
  .footer__logo {
    img {

    }
  }
  .footer__nav {

  }
  .footer__list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-self: start;
    align-items: center;
  }
  .footer__item {
    a {
      color: #fff;
    }
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
  .footer__wrap {

  }
  .footer__phone {
    color: #fff;
    display: flex;
    justify-content: start;
    align-items: center;

    img {
      margin-right: 10px;
    }
  }
  .footer__btn {
    margin-top: 20px;
    color: #fff;
    border-color: #fff;
    padding: 15px 23px;

    color: #FFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;     
  }
  .footer__link {
    color: #fff;

    display: flex;
    justify-content: start;
    align-items: start;

    img {
      margin-right: 10px;
    }
  }
  .footer__socials {

  }
  .footer__social {

  }

  .footer__bottom {
    color: #fff;
    grid-column-start: 1;
    grid-column-end: 5;
  }
  .footer__text {
    color: #fff;
    opacity: 0.5; 
  }
  .footer__bottom-wrap {
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px solid rgb(255, 255, 255, .3);

    display: grid;
    grid-template-columns: 3fr 278px;
    justify-content: space-between;
    gap: 200px;
  }
}
