.header {
  padding: 26px 0 27px;
  background: #fff;
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header__logo {
  
  }
  .header__logo img {
  
  }
  
  .header__wrap {
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .header__nav {
  
  }
  .header__nav-list {
    display: flex;
    justify-content: start;
    align-items: center;
    list-style-type: none;
  }
  .header__nav-item {
    padding: 0;
    a {
      color: #001017;
      font-size: 18px;
      font-family: Muller;
      font-style: normal;
      font-weight: 500;
      line-height: normal;       
    }
    &:not(:last-child) {
      margin-right: 40px;
    }    
  }
  .header__link {
    display: flex;
    justify-content: start;
    align-items: center;

    &:not(:first-child) {
      margin-left: 54px;
    }

    img {
      margin-right: 10px;
    }
  }
  .header__btn {
    margin-left: 30px;
    font-size: 18px;
    padding: 15px 30px;
  }
}
